

.loading-haus {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: loading .7s forwards;
    z-index: 999;
    img {
        max-width: 100%;
    }
}


.grid__item {
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
        animation: wiggle .3s;
    }
}

.grid__item + .large {
    grid-column: span 2;
}

.grid__item + .small img {
    max-width: 77%;
}

.stair2 {
    padding-bottom: 15rem;
    @media only screen and (max-width: 768px) {
    padding-bottom: 0;
    }
}

.phone__hide {
    @media only screen and (max-width: 768px) {
        display: none;
    }
}

@keyframes loading {
    0% {
        opacity: 1;
    }
    99% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes slide {
    0% {
        transform: translateY(0%);
    }
    100% {
        transform: translateY(-100%);
    }
}



@keyframes wiggle {
    0% {
        transform: translateX(0%);
    }
    25% {
        transform: translateX(.5%);
        
    }
    50% {
        transform: translateX(-.5%);
        
    }
    100% {
        transform: translateX(0%);
    }
}
